 * { user-select: none; }

img {
  position: absolute;
  top: 50%; left: 50%;
  /* transform: translate(-50%, -50%); */
}

.scenery {
  width: 100%; height: 50%;
  position: absolute;
  bottom: 0; left: 0;
} 

#foreground1, #foreground2 { z-index: 1 ;}

#foreground2, #background2 {
  transform: translateX(100%);
} 

#palm3 {
  top: -100%; left: 10%;
}

#w_rook_upright {
  top: 15%; left: 75%;
}

#r_pawn {
  top: 10%; left: 15%    
}

#w_rook {
  top: 10%; left: 80%    
}

#r_pawn_upright {
  top: -40%; left: 30%    
}

#r_knight {
  top: -24%; left: 70%    
}

#palm2 {
  top: -70%; left: 90%    
}

#palm1 {
  top: -15%; left: 57%    
}

#bush {
  top: 72%; left: 20%    
}
#red-queen_and_alice {
  position: absolute;
  top: 0; left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  width: 80%;
 max-width: 450px;                         /*   height: 783px / 2; */

  z-index: 1;
}

#red-queen_and_alice:before {
    content: " ";
    display: block;
    padding-top: 87%;
}

#red-queen_and_alice img {
  position: absolute;
  top: 0; left: 0;
  width: 100%;  
}

.sky, .earth {
  position: absolute;
  left: 0;
  height: 50vh; 
  /* height: 92vh; */
  width: 100%;
}

.earth {
  background: #eb125d url( "https://s3-us-west-2.amazonaws.com/s.cdpn.io/641/bg_earth.jpg") repeat-x 0 0 /  100% auto;  
  bottom: 0;
}

.sky {
  background: #246e89 url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/641/bg_sky.jpg") repeat-x  100% 100% / auto 100%;
  top: 0;
}

html, body {
  width: 100%; height: 100%;
}

.wrapper {
  width: 100%; height: 100%;
  overflow: hidden;
  position: absolute;
}